import { useForm, ValidationError } from "@formspree/react";
import "./Contact.scss";

const Contact = () => {
  const [state, handleSubmit] = useForm("mbjqpyzv");
  if (state.succeeded) {
    window.location.reload();
  }
  return (
    <div id="Contact" className="contact">
      <div className="container">
        <div className="wrapper">
          <div className="left">
            <img src="assets/avatar/new77.png" alt="" />
          </div>
          <div className="right">
            <h1>Get in touch</h1>
            <form onSubmit={handleSubmit}>
              <label htmlFor="email"></label>
              <input
                id="email"
                type="email"
                name="email"
                placeholder="Email"
                required
              />{" "}
              <br />
              <ValidationError
                prefix="Email"
                field="email"
                errors={state.errors}
              />
              <textarea
                id="message"
                name="message"
                placeholder="Type your Message Here..."
              />{" "}
              <br />
              <ValidationError
                prefix="Message"
                field="message"
                errors={state.errors}
              />
              <button type="submit" disabled={state.submitting}>
                Holaa...
              </button>
            </form>
          </div>
        </div>
      </div>
      <footer>Dennis Mathew &copy; 2021 Powerd by React.Js</footer>
    </div>
  );
};

export default Contact;
